@media only screen and (min-width: 701px) and (max-width:1400px) {
  .input-search {
    position: absolute;
    top: 10.5%;
    height: 45px;
    width: 50%;
  }
  .main-title {
    margin: 30px 0 20px 0;
    margin-left: 15px;
  }
  .main {
    margin-left: 40px;
  }
  
  .sidenav {
    margin-top: 30px;
    height: 80vh;
  }

}